.IP-body {
    width: 100%;
    height: fit-content;
    background-color: rgba(253,245,234,255);
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px 0px 10px rgba(0, 0, 0, 0.05);
    position: relative;
}

.IP-title {
    width: 80%;
    display: flex;
    justify-content: left;
    margin-bottom: 0.5rem;
}

.IP-title h2 {
    cursor: pointer;
}

.IP-author-date {
    width: 80%;
    height: fit-content;
    display: flex;
    flex-direction: row;
}

.IP-author-date h4{
    margin: 0;
}

.IP-paragraph {
    width: 80%;
    height: fit-content;
    display: flex;
    justify-content: left;
    margin-top: 1rem;
}

.IP-paragraph p {
    margin: 0;
}

.IP-interact {
    width: 80%;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    color: rgb(87, 87, 255);
    align-items: center;
    padding-bottom: 0.5rem;
}

.IP-interact h5 {
    cursor: pointer;
    font-size: 16px;
    margin: 0;
}

.IP-image-container {
    margin-top: 1rem;
    width: 80%;
    height: 200px;
    border-radius: 20px;
    overflow: hidden;
    display: grid;
    cursor: pointer;
}

.IP-image-container.single {
    display: flex;
    
}

.IP-image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.test-image {
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #3b82f6, #8b5cf6);
}

.IP-socials {
    width: 80%;
    height: 50px;
    margin-top: 10px;
    display: flex;
    flex-direction: row;
}

.IP-socials-individual {
    width: fit-content;
    height: 100%;
    margin-right: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
}

.like-icon-wrapper {
    width: fit-content;
    height: 100%;
    margin-right: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    transition: transform 0.2s ease;
}

.like-icon {
    transition: transform 0.5 ease, opacity 0.5s ease;
}

.like-icon.liked {
    animation: pop 0.3s ease;
    color: #e25555;
    width: 30;
}

@keyframes pop {
    0% {
      transform: scale(0.6);
      opacity: 0.5;
    }
    70% {
      transform: scale(1.3);
      opacity: 1;
    }
    100% {
      transform: scale(1);
    }
  }

.IP-author-image {
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 100%;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.IP-author-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.IP-code-display {
    width: 80%;
    height: fit-content;
    max-height: 300px;
    overflow: scroll;
    margin-top: 5px;
    position: relative;
}

.IP-code-display-copy {
    position: absolute;
    top: 8%;
    right: 5px;
    cursor: pointer;
}

.IP-code-display::-webkit-scrollbar {
    width: 0;
}

.IP-code-display pre::-webkit-scrollbar {
    height: 6px; /* horizontal scrollbar */
    width: 6px;  /* vertical scrollbar */
  }
  
  .IP-code-display pre::-webkit-scrollbar-track {
    background: #f0f0f0; 
    border-radius: 10px;
  }
  
  .IP-code-display pre::-webkit-scrollbar-thumb {
    background-color: rgba(100, 100, 100, 0.5);
    border-radius: 10px;
    border: 1px solid #f0f0f0;
  }
  
  .IP-code-display pre::-webkit-scrollbar-thumb:hover {
    background-color: rgba(100, 100, 100, 0.8);
  }

  .IP-tags {
    width: 80%;
    min-height: 30px;
    height: fit-content;
    margin-top: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.5rem;
  }

  .IP-individual-tag {
    display: flex;
    width: fit-content;
    height: 20px;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
    transition: 0.2s all ease-in-out;
  }

  .IP-individual-tag:hover {
    background-color: rgb(87, 87, 255);
    color: white;
    border: 1px solid transparent
  }

  .IP-visibility-icon {
    width: 1rem;
    height: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
  }

  @media (max-width: 500px) {
    .IP-dot {
        font-size: 10px;
    }
  }